import { lazy } from "react";
import { Navigate, Router, useRoutes } from "react-router-dom";


/****Layouts*****/
const FullLayout = lazy(() => import("../layouts/FullLayout.js"));

/***** Pages ****/

const Starter = lazy(() => import("../views/Starter.js"));
const About = lazy(() => import("../views/About.js"));
const Alerts = lazy(() => import("../views/ui/Alerts"));
const Badges = lazy(() => import("../views/ui/Badges"));
const Buttons = lazy(() => import("../views/ui/Buttons"));
const Cards = lazy(() => import("../views/ui/Cards"));
const Grid = lazy(() => import("../views/ui/Grid"));
const Tables = lazy(() => import("../views/ui/Tables"));
const Forms = lazy(() => import("../views/ui/Forms"));
const Breadcrumbs = lazy(() => import("../views/ui/Breadcrumbs"));

// my code 
const AddLawyer = lazy(() => import("../pages/AddLawyer.jsx"));
const UsersList = lazy(() => import("../pages/UsersList.jsx"));
const LoginPage = lazy(() => import("../pages/LoginPage"));
const LawyerMaster=lazy(()=> import("../pages/LawyerMaster"));
const LawyerRequest=lazy(()=> import("../pages/LawyerRequest"))




/*****Routes******/

export default function Routers() {
  return useRoutes([
    {
      path: '/dashboard',
      element: <FullLayout />,
      children: [
        {
          path: "userslist",
          element: <UsersList/>
        },
        {
          path: "forms",
          element: <Forms />
        },
        {
          path: "addlawyer",
          element: <AddLawyer />
        },
        // {
        //   path:'/dashboard',
        //   element: <Navigate to="starter" />
          
        // },
        // {
        //   path:'starter',
        //   exact: true,
        //   element: <Starter />
        // },   
        {
          path:'/dashboard',
          element: <Navigate to="userlist" />
          
        },
        {
          path:'userlist',
          exact: true,
          element: <UsersList />
        },
        {
          path:'lawyerMaster',
          exact: true,
          element: <LawyerMaster />
        },
        {
          path:'lawyerRequest',
          exact: true,
          element: <LawyerRequest />
        }

      ],
    },
    {
      path: '/',
      element: < LoginPage />
    }
  ])
}



